// router/index.js
import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/home/homePage.vue'
const routes = [
  { path: '/', component: Home },
  { path:'/aboutUs', component: () => import('@/views/aboutUs/aboutUs.vue') },
  { path:'/news', component: () => import('@/views/news/newIndex.vue') },
  { path:'/newsDetail', component: () => import('@/views/newsDetail/newsDetail.vue') },
  { path:'/product', component: () => import('@/views/product/productPage.vue') },
  { path:'/productDetail', component: () => import('@/views/productDetail/productDetail.vue') },
  { path:'/job', component: () => import('@/views/job/jobPage.vue') },
  { path:'/jobDetail', component: () => import('@/views/jobDetail/jobDetail.vue') },
  { path:'/contactUs', component: () => import('@/views/contactUs/contactUs.vue') }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    // 始终滚动到顶部
    return { top: 0 }
  },

})

export default router