// store.js
import { createStore } from 'vuex'
import axios from 'axios'


export default createStore({
  state: {
    info: '',
    cloud: null
  },
  mutations: {
    setInfo(state, info) {
      state.info = info
    },
    setCloud(state, cloud){
      state.cloud = cloud
    }
  },
  actions: {
    async fetchInfo({ commit }) {
      let websiteData = {}
      // 注册微信云函数
      const cloud = new window.cloud.Cloud({
        // 必填，表示是未登录模式
        identityless: true,
        // 资源方 AppID
        resourceAppid: 'wxd2b91739417f7a9a',
        // 资源方环境 ID
        resourceEnv: 'ai-weka-cloud-3gtwcbz8ddfef45a',
      })
      await cloud.init()
      try {
          const domain = window.location.host;
          // const domain = "csqysc.ai-site.cc"
          const url = 'http://weka-ai-website-api.ai-site.cc/api/get-website-data?domain=' + domain
          const response = await axios.get(url)
          const responseData = response?.data
          let data = JSON.parse(responseData.resp_data)
          if (data[0]) {
            websiteData = data[0]
            websiteData.news = websiteData.company_news.concat(websiteData.industry_news)
            document.title = websiteData.company_name
            let fileList = []
            if(websiteData.logo.logo_url) {
              fileList.push({
                file: websiteData.logo.logo_url,
                name: 'logo_url'
              })
            }
            if (websiteData.logo.ico_url) {
              fileList.push({
                file: websiteData.logo.ico_url,
                name: 'ico_url'
              })
            }
            if (fileList.length > 0) {
              let res = await cloud.callFunction({
                name: 'pageFunctions',
                data: {
                    method:'getTempFileUrl',
                    fileList: fileList.map(el => el.file)
                }
              })
              if(res.result.code === 200) {
                fileList.forEach((el,index) => {
                  websiteData.logo[el.name] = res.result.data[index].tempFileURL
                })
                if (websiteData.logo.ico_url) {
                  document.getElementById('link').href = websiteData.logo.ico_url
                }
              }
            }
          }
        commit('setInfo', websiteData)
        commit('setCloud', cloud)
      } catch (error) {
        console.error(error)
      }
    }
  }
});