<script setup>
    import { useStore } from 'vuex';
    import { useRouter } from 'vue-router';
    const store = useStore();
    const info = store.state.info;
    const router = useRouter();

    const cutOut = (value) => {
        const random = Math.floor(Math.random() * 51 + 40)
        if(value && value.length > random) {
            return value.substring(0,random) + '...'
        }
        return value
    }

    const timeSplit = (time, type) =>{
        if (time) {
            let arry = time.split("-");
            if(type === 0) {
                return arry[0]
            } else {
                return `${arry[1]}-${ arry[2] }`
            }
        }
        return ""
    }

    const toDetaul = (path, index) => {
        router.push({
            path,
            query:{
                index
            }
        })
    }
</script>
